.App {
  background: #26263A;
  margin: 0px;
  display: flex;
  min-width: 100%;
  min-height: 100%;
  flex-wrap: wrap;
  padding: 12px;
}

.bot-image{
  width: 64px;
  border-radius: 50%;
  margin-right: 20px;
}

.bot{
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 8px;
  background-color: #3A364F;
  margin: 12px;
  color: white;
  width: 320px;
  box-shadow: 8px 8px 20px rgba(0, 0, 0, 0.4), 4px 4px 4px rgba(0, 0, 0, 0.2);
}

.bot-username{
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: 800;
}

.root {
  margin: 0px;
}

p {
  margin-top: 6px;
  margin-bottom: 6px;
}

div {
  margin-bottom: 16px;
}

.type {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 600;
}

h2{
  color: white;
  margin: 12px;
  width: 100%;
}